.top_250_movie_card{
    width: 100%;
    /* height: 15vw; */
    object-fit: cover;
}

.top_250_movie_card img{
    height: 300px;
    width: 200px;
    border-radius: 20px;
    margin: 2px 5px 5px 10px;
    transition: transform 0.3s ease-in;

}

.top_250_movie_card img:hover{
    transform: scale(1.1);

}

.button-color:focus{
    background-color: #ffc400;
    color: black;
}