.main_div{
    width: 100%;
    height: 100%;
    background-color: hsl(240, 100%, 0%);
    display: flex;
  }
  
  .center_div{
    display: flex;
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
  }
  
  .center_div img{
    image-rendering: pixelated;
    padding: 5px;
    border-radius: 30px;
    transition: transform 0.3s ease-in;
    width: 200px;
    height: 300px;
  }
  
  .center_div img:hover{
    transform: scale(1.1);
    cursor: pointer;
  }
  
  .center_div::-webkit-scrollbar{
    overflow: hidden;
  }